import React, { lazy, Suspense } from 'react'
import {
  HashRouter as Router,
  Switch,
  Route,
} from "react-router-dom";

import Preloader from '../components/Preloader/Preloader'

const Card = lazy(() => import('./Card/Card'))

const App = () => {
  return (
    <Router>
        <Suspense fallback={<Preloader />}>
            <Switch>
                <Route exact path="/">
                    <Card />
                </Route>
            </Switch>
        </Suspense>
    </Router>
  )
}

export default App
